  box-sizing: border-box;
  flex-direction: column;
  display: flex;
}

.card__image {
  box-sizing: border-box;
  object-fit: contain;
  max-width: 100%;
  height: 134px;
  margin: 32px auto 40px;
  display: block;
}

.card__title {
  box-sizing: border-box;
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  text-decoration: none;
}

.card__product-code {
  box-sizing: border-box;
  color: #616070;
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating-reviews {
  justify-content: space-between;
  align-items: flex-end;
  height: 16px;
  margin: 16px 0 24px;
  display: flex;
}

.card__stars {
  align-items: center;
  width: 96px;
  height: 16px;
  display: flex;
}

.card__star {
  background: url("star.822d561e.svg") center no-repeat;
